	module.exports = {"data":{"wordpressPost":{"title":"One in five UK NHS staff report bullying by colleagues","content":"<p><a title=\"full text\" href=\"http://www.bmjopen.bmj.com/lookup/doi/10.1136/bmjopen-2013-002628\" target=\"_blank\">One in five UK NHS staff report bullying by colleagues, with almost half saying they have witnessed bullying, in the past six months</a>, indicates research published in <a title=\"BMJ Open\" href=\"http://bmjopen.bmj.com/\" target=\"_blank\">BMJ Open</a>.</p>\n<p>Managers are the most common source of bullying, with workload pressures and organisational culture contributory factors, the study reveals.</p>\n<p>The findings are based on the responses of almost 3000 NHS staff (46% response rate) to a validated questionnaire (NAQ-R), designed to tease out exposure to negative and bullying behaviours. The 12 item General Health Questionnaire (GHQ-12) was also included to assess the impact of bullying on psychological distress.</p>\n<p>All the respondents worked at seven different secondary care, primary care, and mental health NHS trusts in the north east of England. Forty three staff were additionally interviewed in greater depth.</p>\n<p>All types of staff were represented, but the largest proportion of respondents were drawn from support staff, trainee doctors and dentists, healthcare assistants and nurses caring for adults.</p>\n<p>One in five (19.9%) of respondents had been bullied by colleagues within the past 6 months, ranging in frequency from rarely to daily. Medical and dental staff reported the highest levels of bullying (23%).</p>\n<p>Many more (43%) had witnessed other staff being bullied at work at least now and then in the preceding six months, with one in 20 (5%) witnessing it weekly or daily.</p>\n<p>The most commonly reported behaviours were unmanageable workloads; withholding key information; public humiliation; being deliberately ignored; and being shouted at or the target of an angry outburst.</p>\n<p>Over two thirds of respondents (69%) had experienced at least one such behaviour over the past six months, while almost one in five (18%) had had to endure this on a daily or weekly basis.</p>\n<p>Half the respondents (51%) who had been bullied said they had been bullied by a manager/supervisor, and around one in three (31%) accused their peers. Almost one in five staff (18%) blamed workplace culture.</p>\n<p>Men, and those with disabilities, experienced higher levels of negative behaviours, overall.</p>\n<p>Only between 2.7% and 14% of staff reported bullying to someone in authority. The most frequently cited deterrents to reporting a bullying incident included disbelief that anything would change/lack of action by managers; fear of being seen as a trouble-maker; the seniority of the bully; and concern that it would make matters worse.</p>\n<p>Exposure to bullying was associated with psychological distress, as measured by the GHQ-12 survey. It was also associated with lower job satisfaction and increased stated intentions to look for work elsewhere.</p>\n<p>The authors point out that the evidence shows that bullying can have serious repercussions on physical and mental health, and previous research has linked it with a higher risk of error and poorer performance, both of which can affect patient care. Bullying in UK organisations is estimated to cost £13.75 billion a year.</p>\n<p>They conclude: “…In UK healthcare, workplace bullying remains a significant issue with far-reaching consequences for the healthcare workforce,” and add that the recent Francis report into failings at Mid-Staffordshire NHS Foundation Trust highlighted the contributory role of a bullying culture to poor practice.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"d10a8786-326f-5e8d-88d5-9dd226f65c83"}}

